import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import Card from './Card';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.min.css';
import SwiperCore, { Navigation, Pagination } from 'swiper';
import CardSlide from './CardSlide';

SwiperCore.use([Navigation, Pagination]);

const Main = () => {
  return (
    <main className="w-full" style={{color: '#1D2F6F'}}>
      <div className="w-full max-w-screen-xl py-8 mx-auto">
        <div className="flex justify-between px-20">
          <Card
            icon={'i_user_check.svg'}
            titulo={'Capacitaciones'}
            descripcion={'Realizamos capacitaciones en temas de interés en investigación'}
          />
          <Card
            icon={'i_search.svg'}
            titulo={'Investigación'}
            descripcion={'Realizamos capacitaciones en temas de interés en investigación'}
          />
          <Card
            icon={'i_calendar.svg'}
            titulo={'Eventos'}
            descripcion={'Realizamos capacitaciones en temas de interés en investigación'}
          />
        </div>
      </div>
      <div className="w-full bg-gray-200">
        <div className="flex h-full max-w-screen-xl mx-auto">
          <div className="flex-1 px-16 my-auto space-y-2">
            <p className="text-xl font-medium uppercase">Nosotros</p>
            <div className="w-32 h-1 rounded" style={{backgroundColor: '#FAC748'}}></div>
            <div className="space-y-4">
              <p>Somos un grupo estudiantil que tiene como objetivos la promoción, capacitación y realización de investigación en ciencias de la salud con un enfoque multidisciplinario de gran impacto en la salud pública.</p>
              <p>"Más que una asociación, somos una familia"</p>
            </div>
          </div>
          <div className="h-80"><img className="h-80" src="images/img_nosotros_main.jpg" alt="" /></div>
        </div>
      </div>
      <div className="w-full py-12 bg-gray-100 bg-contain bg-opacity-20" style={{backgroundImage: 'url(/images/slide_main.png)'}}>
        <div className="max-w-screen-xl mx-auto bg-white shadow-card w-slide">
          <Swiper slidesPerView={1} pagination={true} navigation={true} className="mySwiper">
            <SwiperSlide>
              <CardSlide img={'images/img_slide_001.png'} />
            </SwiperSlide>
            <SwiperSlide>
              <CardSlide img={'images/img_slide_001.png'} />
            </SwiperSlide>
            <SwiperSlide>
              <CardSlide img={'images/img_slide_001.png'} />
            </SwiperSlide>
            <SwiperSlide>
              <CardSlide img={'images/img_slide_001.png'} />
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </main>
  )
}

export default Main;
