import React from 'react';

const Card = ({icon, titulo, descripcion}) => {
  return (
    <div className="flex px-4 py-6 shadow-card w-80">
      <div className="px-5">
        <img className="h-20" src={`images/${icon}`} alt="" />
      </div>
      <div className="px-2">
        <p className="text-lg font-medium uppercase">{titulo}</p>
        <p className="text-sm">{descripcion}</p>
      </div>
    </div>
  )
}

export default Card;
