import React from 'react';

const Header = () => {
  return (
    <header className="w-full text-white bg-fixed bg-blue-500 bg-bottom bg-cover" style={{backgroundImage: 'url(/images/header.jpg)'}}>
      <nav className="w-full px-20 py-6 border-b border-white">
        <div className="flex items-center justify-between max-w-screen-xl mx-auto">
          <img className="h-20" src="images/logo_adiecs.png" alt="" />
          <ul className="flex text-lg uppercase gap-x-10">
            <li>Inicio</li>
            <li>Nosotros</li>
            <li>Equipo</li>
            <li>Investigación</li>
          </ul>
        </div>
      </nav>
      <div className="w-full py-20">
        <div className="max-w-screen-xl mx-auto">
          <div className="mx-auto" style={{width: '32rem'}}>
            <div className="flex items-center justify-center text-3xl text-center">
              <img className="h-48" src="images/logo_adiecs.png" alt="" />
            </div>
            <div className="pt-2">
              <p className="text-xl text-center">Asociación para el Desarrollo de la Investigación Estudiantil en Ciencias de la Salud</p>
            </div>
          </div>
        </div>
      </div>
    </header>
  )
}

export default Header;
