import React from 'react';

const CardSlide = ({img}) => {
  return (
    <div className="py-12 px-14">
      <div className="flex h-full">
        <div className=""><img className="h-64" src={img} alt="" /></div>
        <div className="flex-1 px-8 my-auto space-y-2 text-center">
          <p className="text-lg font-medium uppercase">Capacitación</p>
          <div className="w-full">
            <div className="w-12 h-1 mx-auto rounded" style={{backgroundColor: '#FAC748'}}></div>
          </div>
          <p className="text-sm leading-tight">Sesión libre a cargo de la Lic. Luz Qquellón con el tema Redacción de un protocolo de investigación</p>
        </div>
      </div>
    </div>
  )
}

export default CardSlide;
