import { faFacebookF, faInstagram, faLinkedinIn, faTwitter, faYoutube } from '@fortawesome/free-brands-svg-icons';
import { faEnvelope, faMapMarkedAlt } from '@fortawesome/free-solid-svg-icons';
import React from 'react';
import Icon from './Stateless/Icon/Icon';

const Footer = () => {
  return (
    <footer className="w-full px-20 py-10 text-white" style={{backgroundColor: '#1D2F6F'}}>
      <div className="grid max-w-screen-xl grid-cols-2 mx-auto">
        <div className="col-span-1">
          <p className="pb-1 text-xl">Contáctanos</p>
          <div className="h-1 bg-white rounded-lg w-28"></div>
          <div className="pt-5 space-y-4">
            <div className="flex">
              <Icon icon={faEnvelope} size={'2x'} />
              <p className="pl-6">adiecs.unmsm@gmail.com</p>
            </div>
            <div className="flex">
              <Icon icon={faMapMarkedAlt} size={'2x'} />
              <div className="pl-6">
                <p>Universidad Nacional Mayor de San Marcos</p>
                <p>Av. Cangallo, Lima - Perú</p>
              </div>
            </div>
          </div>
        </div>
        <div className="col-span-1">
          <p className="pb-1 text-xl">Visítanos</p>
          <div className="h-1 bg-white rounded-lg w-28"></div>
          <div className="pt-5">
            <ul className="flex items-center gap-x-3">
              <li className="flex items-center justify-center w-10 h-10 bg-white rounded-full"><Icon icon={faFacebookF} color={'#1D2F6F'} size={'lg'} /></li>
              <li className="flex items-center justify-center w-10 h-10 bg-white rounded-full"><Icon icon={faInstagram} color={'#1D2F6F'} size={'lg'} /></li>
              <li className="flex items-center justify-center w-10 h-10 bg-white rounded-full"><Icon icon={faYoutube} color={'#1D2F6F'} size={'lg'} /></li>
              <li className="flex items-center justify-center w-10 h-10 bg-white rounded-full"><Icon icon={faLinkedinIn} color={'#1D2F6F'} size={'lg'} /></li>
              <li className="flex items-center justify-center w-10 h-10 bg-white rounded-full"><Icon icon={faTwitter} color={'#1D2F6F'} size={'lg'} /></li>
            </ul>
          </div>
        </div>
      </div>
    </footer>
  )
}

export default Footer;
